@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@400;700&display=swap');

body {
  font-family: 'Quicksand', sans-serif;
  margin: 0px;
  background-color: #f8f8f6 ;
}

.alert {
  background-color: #ff4545;
  margin: 50px;
}

form {
  background-color: #dbd5d5;
  width: 30%;
  height: auto;
  margin: 20px auto;
  padding: 25px;
  border-radius: 2px;
}

input {
  margin: 5px;
  margin-bottom: 10px;
}

.log-in,
.log-out,
.register {
  background-color: #d6c0a6;
  border-radius: 4px;
  color: #f8f8f8;
  min-height: 50px;
  min-width: 100px;
  outline: 0;
  cursor: pointer;
}

.log-in:hover,
.log-out:hover,
.register:hover {
  background-color: #d6c0a6;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  outline: 0;
  border-bottom: 1px solid #444;
  cursor: pointer;
}

.content-page{
  margin-top: 80px;
  
}

